*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}

html {
    font-size: 10px;
}

@media only screen and (min-width: 35.5em) {
    html {
        font-size: 12px;
    }
}

@media only screen and (min-width: 48em) {
    html {
        font-size: 14px;
    }
}

@media only screen and (min-width: 64em) {
    html {
        font-size: 16px;
    }
}

@media only screen and (min-width: 80em) {
    html {
        font-size: 18px;
    }
}

@media only screen and (min-width: 120em) {
    html {
        font-size: 20px;
    }
}

@media only screen and (min-width: 160em) {
    html {
        font-size: 22px;
    }
}

@media only screen and (min-width: 240em) {
    html {
        font-size: 24px;
    }
}

body {
    height: 100vh;
    width: 100vw;

    display: flex;
    justify-content: center;
    align-items: center;
}

.sliderContainer {
    position: relative;

    display: flex;
    width: 20rem;
    height: 20rem;
}

.imageFrame {
    flex: 1;

    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: clip;

    position: relative;
}

.imageDiv {
    height: 100%;
    flex: 1;
    display: none;

    opacity: 0;
}
.imageDiv[data-active="true"] {
    display: block;
    opacity: 1;
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.sliderButton {
    align-self: center;
    position: absolute;
    height: 2rem;
    width: 2rem;
    background-color: #333;
    border-radius: 50%;
    opacity: 0.7;
    cursor: pointer;
}

#nextButton {
    right: 0;
    transform: translateX(150%);
}

#prevButton {
    left: 0;
    transform: translateX(-150%);
}

#nextButton::after,
#prevButton::after {
  content: '';
  position: absolute;
  border: solid white;
  border-width: 0 0.2rem 0.2rem 0;
  display: inline-block;
  padding: 0.25rem;
  width: 40%;
  height: 40%;
}

#nextButton::after {
    top: 50%;
    right: 50%;
    transform: translate(25%, -50%) rotate(-45deg);
}

#prevButton::after {
    top: 50%;
    right: 50%;
    transform: translate(75%, -50%) rotate(135deg);
}

.sliderNav {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
}

.imageDot {
    display: inline-block;
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    border: 0.1rem #fff solid;
    background-color: #333;
    opacity: 0.8;
    margin: 0 1rem 0 1rem;
    cursor: pointer;
}

.imageDot[data-active="true"] {
    background-color: #fff;
    border: 0.1rem #333 solid;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;IAGI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI,aAAa;IACb,YAAY;;IAEZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;;IAElB,aAAa;IACb,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,OAAO;;IAEP,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;IACnB,mBAAmB;;IAEnB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,OAAO;IACP,aAAa;;IAEb,UAAU;AACd;AACA;IACI,cAAc;IACd,UAAU;AACd;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,QAAQ;IACR,2BAA2B;AAC/B;;AAEA;IACI,OAAO;IACP,4BAA4B;AAChC;;AAEA;;EAEE,WAAW;EACX,kBAAkB;EAClB,mBAAmB;EACnB,+BAA+B;EAC/B,qBAAqB;EACrB,gBAAgB;EAChB,UAAU;EACV,WAAW;AACb;;AAEA;IACI,QAAQ;IACR,UAAU;IACV,8CAA8C;AAClD;;AAEA;IACI,QAAQ;IACR,UAAU;IACV,8CAA8C;AAClD;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,SAAS;IACT,gCAAgC;AACpC;;AAEA;IACI,qBAAqB;IACrB,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,yBAAyB;IACzB,sBAAsB;IACtB,YAAY;IACZ,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,sBAAsB;IACtB,yBAAyB;AAC7B","sourcesContent":["*,\n*::after,\n*::before {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    user-select: none;\n}\n\nhtml {\n    font-size: 10px;\n}\n\n@media only screen and (min-width: 35.5em) {\n    html {\n        font-size: 12px;\n    }\n}\n\n@media only screen and (min-width: 48em) {\n    html {\n        font-size: 14px;\n    }\n}\n\n@media only screen and (min-width: 64em) {\n    html {\n        font-size: 16px;\n    }\n}\n\n@media only screen and (min-width: 80em) {\n    html {\n        font-size: 18px;\n    }\n}\n\n@media only screen and (min-width: 120em) {\n    html {\n        font-size: 20px;\n    }\n}\n\n@media only screen and (mi